<template>
	<v-layout>
		<mu-carousel>
			<mu-carousel-item v-for="item in imgList">
				<img :src="item.a_url" />
			</mu-carousel-item>
		</mu-carousel>
		<mu-paper :z-depth="1" class="demo-loadmore-wrap">
			<mu-container ref="container" class="demo-loadmore-content">
	    		<mu-load-more :refreshing="refreshing" :loading="loading" loading-text="数据加载中，请稍后..." :loaded-all="loadedAll" @load="load">
					<mu-grid-list class="gridlist-demo" :cols="5">
					  <mu-grid-tile v-for="(item,index) in videoList" :key="index">
					  	<router-link :to="'/detail/' + item.v_id">
							<img :src="item.v_preview_url" >
						</router-link>
						<span slot="title">{{item.v_name}}</span>
						<mu-button slot="action" icon>
						  <mu-icon value="star_border"></mu-icon>
						</mu-button>
					  </mu-grid-tile>
					</mu-grid-list>
				</mu-load-more>
			</mu-container>
		</mu-paper>
	</v-layout>
</template>

<script>
import axios from 'axios';
import layout from './layout';
export default {
	name: 'index',
	components: {
		'v-layout': layout
	},
	data() {
		return {
			imgList: [],
			getImgUrl: 'http://my.weixin.com/index.php/api-getad',
			videoList: [],
			getVideoUrl: 'http://my.weixin.com/index.php/api-getVideo',
			refreshing: false,
		    loading: false,
		    loadedAll: false,
		    page: 1,
		    limit: 15
		}
	},
	created: function() {
		this.getCarouselImg();
		this.getVideo();
	},
	methods: {
		getCarouselImg: function() {
			var that = this;
			axios.get(that.getImgUrl).then(function(res){
				that.imgList = res.data;
			})
		},
		getVideo: function() {
			var that = this;
			axios.get(that.getVideoUrl, {
				params: {
					p: that.page,
					l: that.limit
				}
			}).then(function(res){
				if (res.data.length > 0) {
					if (that.videoList.length > 0) {
						for (let item of res.data) {
							that.videoList.push(item);
						}
					}else {
						that.videoList = res.data;
					}
					that.page++;
				}else {
					that.loadedAll = true;
					alert('已经到底啦！');
				}
			})
		},
		load: function() {
			this.loading = true;
			var that = this;
			setTimeout(function() {
				that.getVideo();
				that.loading = false;
		    }, 2000)
		}
	}
}
</script>

<style>
@media (min-width: 1200px) .container {
    max-width: 100% !important;
}
.demo-loadmore-wrap {
  width: 100%;
  display: flex;
  flex-direction: column;
  .mu-appbar {
    width: 100%;
  }
}
.demo-loadmore-content {
  flex: 1;
  overflow: visible;
  -webkit-overflow-scrolling: touch;
}
</style>




