<template>
	<div id="header">
		<link rel="stylesheet" href="https://cdn.bootcss.com/material-design-icons/3.0.1/iconfont/material-icons.css">
		<mu-appbar style="width: 100%;" color="primary">
		  <mu-button icon slot="left" @click="open = !open">
			<mu-icon value="menu"></mu-icon>
		  </mu-button>
		  我的第一个vue网站
		  <mu-menu slot="right">
			<mu-button flat>欢迎：vivi</mu-button>
			<mu-list slot="content">
			  <mu-list-item button>
				<mu-list-item-content>
				  <mu-list-item-title>我的</mu-list-item-title>
				</mu-list-item-content>
			  </mu-list-item>
			  <mu-list-item button>
				<mu-list-item-content>
				  <mu-list-item-title>退出</mu-list-item-title>
				</mu-list-item-content>
			  </mu-list-item>
			</mu-list>
		  </mu-menu>
		</mu-appbar>
		<mu-drawer :open.sync="open" :docked="docked" :right="position === 'right'">
		    <mu-list>
		      <mu-list-item button :to="'/'">
		      	<router-link :to="'/'">
		        	<mu-list-item-title>首页</mu-list-item-title>
		        </router-link>
		      </mu-list-item>
		      <mu-list-item button>
		        <mu-list-item-title>捐赠我们</mu-list-item-title>
		      </mu-list-item>
		      <mu-list-item button :to="'/aboutus'">
		      	<router-link :to="'/aboutus'">
		        	<mu-list-item-title>关于我们</mu-list-item-title>
		        </router-link>
		      </mu-list-item>
		      <mu-list-item  @click="open = false" button>
		        <mu-list-item-title>关闭</mu-list-item-title>
		      </mu-list-item>
		    </mu-list>
	  </mu-drawer>
	</div>
</template>

<script>
export default {
	name: 'gheader',
	data() {
		return {
			docked: false,
			open: false,
			position: 'left'
		}
	}
}
</script>


