<template>
	<div id="layout">
		<v-header></v-header>
		<slot></slot>
		<v-footer></v-footer>
	</div>
</template>

<script>
import header from './header';
import footer from './footer';
export default {
	name: 'layout',
	components: {
		'v-header': header,
		'v-footer': footer
	}
}
</script>


