import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import detail from '@/components/detail'
import aboutus from '@/components/aboutus'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
        path: '/detail/:id',
        name: 'detail',
        component: detail
    },
    {
        path: '/aboutus',
        name: 'aboutus',
        component: aboutus
    }
  ]
})



